
 const Domain = 'http://testtruckminiapp.huoyunjh.cn'; //  
 const hddDomain = 'http://news.huoyunjh.com'; //  ()
 const crashDomain = 'http://testtruck.huoyunjh.cn'; //  ()

// const Domain = 'https://truckminiapp.huoyunjh.com';    //  ()
// const hddDomain = 'https://www.huoyunjh.com'; //  ()
// const crashDomain = 'https://truck.huoyunjh.com'; //  ()

export const WXFetch = (url, params, isShowLoading = true , method = 'POST' , type = 1) => {
  let preUrl = Domain
  if (type == 2) {
    preUrl = hddDomain
  }
  if (type == 3) {
    preUrl = crashDomain
  }
  if (isShowLoading) {
    uni.showLoading({
      title: ''
    })
  }
  return new Promise((resolve, reject) => {
    uni.request({
      url: preUrl + url,
      data: params,
      method: method,
      header: {
        'content-type': 'application/x-www-form-urlencoded',
        'Cookie': 'JSESSIONID=' + uni.getStorageSync('jsessionId')
      },
      success: resolve,
      fail: reject,
      complete: function () {
        if (isShowLoading) {
          uni.hideLoading()
        }
      }
    })
  })
}

export const otherRequest = (url, params, method = 'POST') => {
  uni.showLoading({
    title: ''
  })
  return new Promise((resolve, reject) => {
    uni.request({
      url: url,
      data: params,
      method: method,
      header: {
        'content-type': 'application/x-www-form-urlencoded',
        'Cookie': 'JSESSIONID=' + uni.getStorageSync('jsessionId')
      },
      success: resolve,
      fail: reject,
      complete: function () {
        uni.hideLoading()
      }
    })
  })
}

export const returnDomain = () => {
  return Domain
}